  src: url("Stardew Valley Stonks.822eb553.otf");
}

:root {
  font: var(--font-size, 100%) / 1.25 Stardew Valley Stonks, Nunito, sans;
  --space-width: calc(3rem / 13);
  --icon-scale: var(--icon-base-scale);
  --arrow-length: calc(var(--icon-scale) * 11px);
  --arrow-width: calc(var(--icon-scale) * 10px);
}

body {
  min-height: 100vh;
  max-height: 100vh;
  background-image: url("Gradient.8e926989.png");
  background-repeat: repeat-x;
  background-size: contain;
  flex-direction: column;
  margin: 0;
  display: flex;
}

body > span {
