  height: calc(var(--icon-scale) * 18px);
  background: url("Range Thumb.42a349b0.png") center / contain no-repeat;
  border: none;
  border-radius: 0;
}

input[type="range"]::-moz-range-thumb {
  image-rendering: crisp-edges;
  box-shadow: none;
  width: calc(var(--icon-scale) * 30px);
  height: calc(var(--icon-scale) * 18px);
  background: url("Range Thumb.42a349b0.png") center / contain no-repeat;
  border: none;
  border-radius: 0;
}

input[type="range"]::-webkit-slider-runnable-track {
  image-rendering: pixelated;
  height: calc(var(--icon-scale) * 18px);
  border-image-source: url("Range Track.2171eb37.png");
  border-image-slice: 2 fill;
  border-image-width: calc(var(--icon-scale) * 6px);
}

input[type="range"]::-moz-range-track {
  image-rendering: crisp-edges;
  height: calc(var(--icon-scale) * 18px);
  border-image-source: url("Range Track.2171eb37.png");
  border-image-slice: 2 fill;
  border-image-width: calc(var(--icon-scale) * 6px);
}

main {
  flex-flow: row;
  flex-grow: 1;
  display: flex;
  overflow: hidden;
}

main > section {
  min-width: min-content;
  flex-direction: column;
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  overflow: hidden;
}

main > section > [role="tabpanel"] {
  flex-direction: column;
  flex-grow: 1;
  display: flex;
  overflow: auto;
}

.graph {
  flex-grow: 1;
  overflow: hidden;
}

.graph-controls {
  justify-content: center;
  align-items: center;
  display: flex;
}

.recharts-wrapper {
