.calendar-season {
  min-width: min-content;
  padding: .5rem 1rem;
}

.calendar-header {
  --icon-scale: calc(var(--icon-base-scale) * 2);
  justify-content: center;
  align-items: center;
  margin-bottom: .25rem;
  font-size: 2rem;
  display: flex;
}

.calendar-days {
  gap: var(--eighth-border);
  border: var(--eighth-border) solid black;
  grid-template-columns: repeat(7, minmax(4rem, 1fr));
  grid-auto-rows: 1fr;
  display: grid;
}

.calendar-days > div {
  aspect-ratio: 1;
  outline: var(--eighth-border) solid black;
  justify-content: center;
  align-items: center;
  padding: 10%;
  display: flex;
  position: relative;
}

.calendar-days > div > img {
  width: 50%;
  height: unset;
}

.calendar-days > div:nth-child(28n+1) {
  counter-reset: day;
}

.calendar-days > div:before {
  counter-increment: day;
  content: counter(day);
  position: absolute;
  top: 2.5%;
  left: calc(2.5% + .25rem);
}

.input-box {
  -moz-appearance: textfield;
  background: none;
  border-image-source: url("Input Box.b4b7bfd3.png");
  border-image-slice: 3 fill;
  border-image-width: .5rem;
  outline: none;
  padding: .5rem;
}

.input-box::-webkit-outer-spin-button, .input-box::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.select {
  --border: #5b2b2a solid var(--sixth-border);
  cursor: pointer;
  display: inline-flex;
}

.select[aria-expanded="true"]:focus-within {
  outline: none;
}

.select[aria-expanded="false"]:hover > div > .select-control {
