  border: var(--border);
  background-color: #f9ba66;
  align-items: center;
  padding: 0 .5rem;
  display: flex;
  position: relative;
}

.select-control > input[type="search"] {
  width: calc(100% - 1rem);
  height: 100%;
  background: none;
  border: none;
  outline: none;
  padding: 0;
  position: absolute;
}

.select-control > input[type="search"]:not(:placeholder-shown) + * {
  visibility: hidden;
}

.select-control span {
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline;
  overflow: hidden;
}

.select-list {
  max-height: 40vh;
  width: 100%;
  z-index: 1;
  border: var(--border);
  box-sizing: border-box;
  background-color: #f9ba66;
  border-top: none;
  position: absolute;
  top: 100%;
  left: 0;
  overflow: auto;
}

.select-list > li {
  padding: .5rem;
